* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  border-radius: 3px;
}

#search-box:hover,
#search-box:focus {
  outline: none;
  border: 2px solid rgb(44, 162, 216);
}

.main-data {
  display: flex;
  align-items: center;
}

.flag-icon {
  margin-right: 20px;
  margin-left: 20px;
}
